<script>
import HelloWorld from './components/HelloWorld.vue'

export default {
  name: 'App',
  components: {
    HelloWorld
  },
  data () {
    return {
      rc: 0,
      other: 0,
      torn: 0,
      brmo: 0
    }
  },
  mounted () {
    this.process_counts()
  },
  methods: {

    process_counts () {
      const counters = ['rc', 'other', 'torn', 'brmo']
      this.rc = 0
      this.other = 0
      this.torn = 0
      this.brmo = 0
      const today = new Date().toLocaleDateString()
      for (const key in localStorage) {
        for (const x in counters) {
          const counter = counters[x]
          if (key.includes(counter)) {
            const keyBits = key.split('-')
            const dt = keyBits[1]
            if (dt.includes(today)) {
              const amountToAdd = parseInt(localStorage.getItem(key))
              this[counter] += amountToAdd
            } else {
              console.log('no')
              // put in check for already yesterdays stuff, if not there create one, otherwise just continue
            }
            // console.log('yes')
          } else {
            console.log('no')
          }
        }
      }
    },

    countIt (which, typ, amount) {
      const now = new Date().toLocaleString()
      const key = which + '-' + now
      let toSaveDown = 0
      if (typ === 'plus') {
        toSaveDown += amount
      } else if (typ === 'minus') {
        toSaveDown -= amount
      }
      localStorage.setItem(key, toSaveDown)
      this.process_counts()
    }

  }
}
</script>

